export default {
	data() {
		return {
			lists:[],
			logined:false
		};
	},
	onLoad() {
		var me = this;
		var userinfo = me.getGlobalUser("userInfos");
		me.logined = userinfo.logined;
		uni.request({
			url: this.mainUrl+'user/user/getAttendOrdersByUserId',
			method:"GET",
			data:{
				user_id:userinfo.userId
			},
			success: (res) => {
				console.log("res");
				if(res.data.status == "success"){
					console.log(res);
					var lists = res.data.data;
					console.log("lists:");
					console.log(lists);
					for(var i=0;i<lists.length;i++){
						uni.request({
							url:this.mainUrl+'order/order/findOrderInfoById',
							method:"GET",
							data:{
								order_id:lists[i].orderId
							},
							success:(listres) => {
								console.log(listres);
								if(listres.data.data.status == 3){
									me.lists.push(listres.data.data);
								}
							}
						})
					}	
				}else{
					console.log("err:"+res.data.data.errMsg);
				}
			}
		});	
	},
	methods:{
		login(){
			uni.navigateTo({
				url:"../../me_login/me_login"
			})
		}
	}
}
